@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300;900&display=swap');

@media (max-width: 768px) {
  .parentaboutme {
      grid-template-columns: repeat(1, 1fr);
      grid-template-rows: repeat(6, 1fr);
  }

  .divCentral, .divlefttopcorner, .divleftbottomcorner, .divrightbottomcorner, .divrighttopcorner {
      grid-column: 1 / 2;
  }

  .divCentral {
      grid-row: 1 / 2;
  }

  .divlefttopcorner {
      grid-row: 2 / 3;
  }

  .divleftbottomcorner {
      grid-row: 3 / 4;
  }

  .divrightbottomcorner {
      grid-row: 4 / 5;
  }

  .divrighttopcorner {
      grid-row: 5 / 6;
  }

  .container, .container1, .container2, .container3 {
      width: 90%;
      left: 5%;
      right: 5%;
  }
}


.aboutme
{
    background-color: $white;
    height: auto;

    .parentaboutme{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, 1fr);
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        justify-content:center;

        height: 100%;

        .divCentral 
        { 
            grid-area: 2 / 2 / 3 / 3;
            background-color: $white;

            // display: flex;
            // justify-content: center;
            // align-items: center;

            display: grid;
            font-family: 'Poppins', sans-serif;
            font-size: 27px;
            font-weight: 700;
            // height: 100vh;
            place-items: center;

            h1 {
              background-image: linear-gradient(
                to right,
                #aa02dd,
                #3773e2 50%,
                #3773e2 50%
              );
              background-size: 200% 100%;
              background-position: -100%;
              display: inline-block;
              padding: 5px 0;
              position: relative;
              -webkit-background-clip: text;
              -webkit-text-fill-color: transparent;
              transition: all 0.3s ease-in-out;
            }

            h1:before{
              content: '';
              background: $blue;
              display: block;
              position: absolute;
              bottom: -3px;
              left: 0;
              width: 0;
              height: 3px;
              transition: all 0.3s ease-in-out;
            }

            h1:hover {
              background-position: 0;
             }
             
             h1:hover::before{
               width: 100%;
             }
             

            
        }
        .divlefttopcorner 
        { 
            grid-area: 1 / 1 / 2 / 2;
            background: $white;
            

            .container  {
                position: absolute;
                left: 10%;
                // top: 17%;
                // transform: translate(-50%, -50%);
                height: 200px;
                width: 450px;
                background: #ffffff;
                overflow: hidden;
                border-radius: 20px;
                cursor: pointer;
                box-shadow: rgba(0, 250, 205, 0.775) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;

                .content {
                    position: absolute;
                    text-align: justify;
                    color: black;
                    padding: 20px;
                    font-family: $Montfont;

                    h1 {
                        font-weight: 900;
                        text-align: center;
                      }

                      h3 {
                        font-weight: 300;
                      }
                      
                  }

                  .flap {
                    width: 100%;
                    height: 100%;
                  }

                  .flap::before {
                    position: absolute;
                    content: "";
                    height: 100%;
                    width: 50%;
                    background: url("https://pbs.twimg.com/profile_images/1347260174176710658/2GfSZ1i__400x400.jpg") white;
                    background-position: 100px;
                    background-repeat: no-repeat;
                    transition: 1s;
                  }
                  
                  .flap::after {
                    position: absolute;
                    content: "Mindset";
                    font-family: $title-font;
                    font-size: larger;
                    color: $blue ;
                    display: flex;
                    align-items: center;
                    height: 100%;
                    width: 50%;
                    right: 0;
                    background: url("https://pbs.twimg.com/profile_images/1347260174176710658/2GfSZ1i__400x400.jpg") white;
                    background-position: -200px;
                    background-repeat: no-repeat;
                    transition: 1s;
                  }


            }

            .container:hover .flap::after {
                transform: translatex(300px);
              }
              
              .container:hover .flap::before{
                transform: translatex(-300px);
            }


        }

